<template>
  <div class="demo-item">
    <h3 class="demo-item-title">
      {{ name }}
      <small>{{ description }}</small>
      <div class="show-code-btn" @click="show_code = !show_code">Code</div>
    </h3>
    <hr />
    <div class="demo-item-container">
      <slot></slot>
    </div>
    <transition name="slide">
      <v-code v-show="show_code" :lang="lang" :code="code"></v-code>
    </transition>
  </div>
</template>

<style lang="stylus" scoped>
  @import '../../style/index.styl';

  _demo_gap = 5px;

  .demo-item {
    display: flex;
    flex-direction: column;
    margin: 10px;
    padding: _demo_gap;
    background-color: _white;
    border-radius: 4px;
    box-shadow: 0 0 1px 1px rgba(0, 0, 0, .16);
  }

  .demo-item-container {
    padding: 10px _demo_gap;
  }

  .demo-item-title {
    height: 30px;
    line-height: 30px;
    padding: 0 _demo_gap;

    small {
      margin-left: 5px;
      font-size: 12px;
      color: #999;
    }

    .show-code-btn {
      float: right;
      font-size: 12px;
      color: _blue;
      cursor: pointer;
    }
  }

  .slide-enter-active, .slide-leave-active {
    max-height: 300px;
    overflow: hidden;
    transition: all .3s ease;
  }
  .slide-enter, .slide-leave-to {
    max-height: 0;
  }
</style>

<script>
  export default {
    name: 'demo-item',
    components: {
      vCode: require('./v-code')
    },
    props: {
      lang: {
        default: 'html',
        type: String
      },
      name: String,
      description: String,
      code: String
    },
    data () {
      return {
        show_code: false
      }
    }
  }
</script>



